.github-contri-wrapper{
    height: fit-content;
    width: 55%;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
    color: white;
}

.github-contributions{
    margin-top: 20px;
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.description{
    margin-top: 120px;
    background: rgba(255, 255, 255, 0.063);
    border-radius: 8px;
    padding: 20px;
    width: fit-content;
    left: 50%;
    transform: translate(-50%);
    position: relative;
    max-height: calc(70vh - 400px);
    overflow-y: hidden;
    transition: opacity 3s ease 0s;
}

.experience-item{
    border-radius: 50%;
    text-align: center;
    position: relative;
    background: white;
    cursor: pointer;
}

.experience-item > img{
    width: 50px;
    border-radius: 50%;
    margin: 5px;
    transition: all 1s ease 0s;
}

.experience-item-details{
    position: absolute;
    transform: translateX(-50%);
    left: 35px;
    color: white;
    width: 200px;
    margin-top: 10px;
}